import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, FormGroupDirective, NgForm } from '@angular/forms';
import { CountryService } from 'src/app/services/country.service';
import {ErrorStateMatcher} from '@angular/material/core';
import { Country } from 'src/app/domain/country';

export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}

@Component({
  selector: 'app-inscripcion',
  templateUrl: './inscripcion.component.html',
  styleUrls: ['./inscripcion.component.css']
})
export class InscripcionComponent implements OnInit {

  formGroup: FormGroup;
  matcher = new MyErrorStateMatcher();
  submitted = false;
  grados = [
    'Estudiante',
    'Becario',
    'Post-doc',
    'Investigador'
  ];

  addingPoster = false;
  addingPresentacion = false;
  countries: Country[] = [];


  constructor(private fb: FormBuilder, private countryService: CountryService) { }

  ngOnInit() {
    this.formGroup = this.fb.group( {
        name: ['', Validators.required],
        institucion: ['', Validators.required],
        grado: ['', Validators.required],
        email: ['', [Validators.required, Validators.email]],
        city: ['', Validators.required],
        country: [13, Validators.required]
      });

      this.countryService.getList().subscribe(countries => {
        this.countries = countries;
      });

  }

  submit() {
    if(this.formGroup.valid){
      this.submitted = true;
    }
  }

  addPresentacion(){
    this.addingPresentacion = true;
  }

  addPoster(){
    this.addingPoster = true;
  }

  closePresentacion(){
    this.addingPresentacion = false;
  }

  closePoster(){
    this.addingPoster = false;
  }


}

export enum Grado {
  Estudiante = 'Estudiante',
  Becario = 'Becario',
  PostDoc = 'Post-doc',
  Investigador = 'Investigador'
}
