import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { InscripcionService } from 'src/app/services/inscripcion.service';
import { Topic } from 'src/app/domain/topic';
import { FormGroup, FormBuilder, Validators, FormControl, FormGroupDirective, NgForm } from '@angular/forms';

import {ErrorStateMatcher} from '@angular/material/core';
export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}

@Component({
  selector: 'app-envio-posters',
  templateUrl: './envio-posters.component.html',
  styleUrls: ['./envio-posters.component.css']
})

export class EnvioPostersComponent implements OnInit {

  constructor(private fb: FormBuilder, private inscripcionService: InscripcionService) { }
  @Input() embedded = false;
  @Output() closed = new EventEmitter<boolean>();

  formGroup: FormGroup;
  topics: Topic[];
  submitted = false;
  matcher = new MyErrorStateMatcher();
  filename = '';
  file: any;
  fileError = false;
  ngOnInit() {

    this.inscripcionService.getTopics().subscribe(tps => this.topics = tps);

    this.formGroup = this.fb.group( {
        email: ['', [Validators.required, Validators.email]],
        title: ['', Validators.required],
        author: ['', Validators.required],
        category: ['', [Validators.required]],
        printed: [13, Validators.required]
      });

      if(this.embedded){
        this.formGroup.removeControl('email');
      }


  }

  uploadFile(event) {
    const reader = new FileReader();
    const file: File = event.target.files[0] as File;
    this.filename = file.name;
    reader.readAsText(file);

    reader.onload = e => { // called once readAsDataURL is completed
        this.file = reader.result;
    }
  }

  submit() {
    if(this.file){

    }
    else{
      this.fileError = true;
    }
  }

  close(){
    this.closed.emit(true);
  }

}
