import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MaterialModule } from '../material.module';
import { SharedModule } from '../shared/shared.module';
import { InscripcionComponent } from './components/inscripcion/inscripcion.component';
import { EnvioPostersComponent } from './components/envio-posters/envio-posters.component';
import { ContribucionOralComponent } from './components/contribucion-oral/contribucion-oral.component';
import { ListaParticipantesComponent } from './components/lista-participantes/lista-participantes.component';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { PresentacionOralComponent } from './components/presentacion-oral/presentacion-oral.component';

const routes: Routes = [
  { path: '', component: InscripcionComponent },
  { path: 'posters', component: EnvioPostersComponent },
  { path: 'contribucion-oral', component: ContribucionOralComponent },
  { path: 'participantes', component: ListaParticipantesComponent },
  { path: 'presentacion-oral', component: PresentacionOralComponent },
];


@NgModule({
  declarations: [InscripcionComponent, EnvioPostersComponent, ContribucionOralComponent, ListaParticipantesComponent, PresentacionOralComponent],
  imports: [
    CommonModule,
    MaterialModule,
    SharedModule,
    RouterModule.forChild(routes),
    FormsModule,
    ReactiveFormsModule,
  ]
})
export class RegistroModule { }
