import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Topic } from '../domain/topic';

@Injectable({
  providedIn: 'root'
})
export class InscripcionService {

  constructor() { }

  topics: Topic[] = [
    new Topic(1, 'Astrofísica Estelar \(AE\)'),
    new Topic(2, 'Astronomía Extragaláctica y Cosmología \(AEC\)'),
    new Topic(3, 'Estructura Galáctica \(EG\)'),
    new Topic(4, 'Historia, Educación y Divulgación de la Astronomía \(HEDA\)'),
    new Topic(5, 'Instrumentación y Caracterización de Sitios Astronómicos \(ICSA\)'),
    new Topic(6, 'Medio Interestelar \(MI\)'),
    new Topic(7, 'Objetos Compactos y Procesos de Altas Energías \(OCPAE\)'),
    new Topic(8, 'Sistemas Estelares \(SE\)'),
    new Topic(9, 'Sistemas Solar y Extrasolares \(SSE\)'),
    new Topic(10, 'Sol y Heliósfera \(SH\)'),
    new Topic(11, 'Otros'),

  ]

  getTopics(): Observable<Topic[]>{
    return of(this.topics);
  }
}

